import React, { useState, useEffect } from "react";
import { Button, Table, Modal, Form, Spinner } from "react-bootstrap";
import axios from "axios";

const ShippingPage = () => {
  const [orders, setOrders] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [modalType, setModalType] = useState(""); // Action type
  const [formData, setFormData] = useState({});
  const [loading, setLoading] = useState(false);
  const [processing, setProcessing] = useState(false);

  // Fetch orders on component mount
  useEffect(() => {
    fetchOrders();
  }, []);

  const fetchOrders = async () => {
    setLoading(true);
    try {
      const response = await axios.get("/api/orders"); // Replace with your actual endpoint
      setOrders(response.data);
    } catch (error) {
      console.error("Error fetching orders:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleAction = async (actionType) => {
    setProcessing(true);
    try {
      if (actionType === "update") {
        await axios.put(`/api/shiprocket/${formData.shipmentId}`, formData);
        console.log("Shipment details updated");
      } else if (actionType === "cancel") {
        await axios.post(`/api/shiprocket/${formData.shipmentId}/cancel`);
        console.log("Shipment canceled");
      } else if (actionType === "return") {
        await axios.post(`/api/shiprocket/${formData.shipmentId}/return`);
        console.log("Return initiated");
      }
      fetchOrders(); // Refresh orders
      setShowModal(false);
    } catch (error) {
      console.error(`Error with ${actionType}:`, error.response?.data || error.message);
    } finally {
      setProcessing(false);
    }
  };

  const generateLabelOrInvoice = async (type, shipmentId) => {
    try {
      const endpoint = `/api/shiprocket/${shipmentId}/${type}`; // Replace with actual endpoint
      const response = await axios.get(endpoint, { responseType: "blob" });
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", `${type}-${shipmentId}.pdf`);
      document.body.appendChild(link);
      link.click();
    } catch (error) {
      console.error(`Error generating ${type}:`, error);
    }
  };

  return (
    <div className="container mt-5">
      <h1>Shipping Management</h1>
      {loading ? (
        <Spinner animation="border" />
      ) : (
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>Order ID</th>
              <th>Recipient Name</th>
              <th>Address</th>
              <th>Status</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order) => (
              <tr key={order.id}>
                <td>{order.id}</td>
                <td>{order.recipientName}</td>
                <td>{order.address}</td>
                <td>{order.status}</td>
                <td>
                  <Button
                    variant="secondary"
                    onClick={() => {
                      setModalType("update");
                      setFormData({
                        shipmentId: order.shipmentId,
                        recipientName: order.recipientName,
                        address: order.address,
                      });
                      setShowModal(true);
                    }}
                  >
                    Update Shipment Details
                  </Button>{" "}
                  <Button
                    variant="danger"
                    onClick={() => {
                      setModalType("cancel");
                      setFormData({ shipmentId: order.shipmentId });
                      handleAction("cancel");
                    }}
                  >
                    Cancel Shipment
                  </Button>{" "}
                  <Button
                    variant="warning"
                    onClick={() => {
                      setModalType("return");
                      setFormData({ shipmentId: order.shipmentId });
                      handleAction("return");
                    }}
                  >
                    Return Order
                  </Button>{" "}
                  <Button
                    variant="info"
                    onClick={() => generateLabelOrInvoice("label", order.shipmentId)}
                  >
                    Generate Label
                  </Button>{" "}
                  <Button
                    variant="success"
                    onClick={() => generateLabelOrInvoice("invoice", order.shipmentId)}
                  >
                    Generate Invoice
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      )}

      {/* Modal for actions */}
      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>
            {modalType === "update" && "Update Shipment Details"}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            {modalType === "update" && (
              <>
                <Form.Group>
                  <Form.Label>Recipient Name</Form.Label>
                  <Form.Control
                    type="text"
                    name="recipientName"
                    value={formData.recipientName || ""}
                    onChange={handleFormChange}
                    required
                  />
                </Form.Group>
                <Form.Group>
                  <Form.Label>Address</Form.Label>
                  <Form.Control
                    type="text"
                    name="address"
                    value={formData.address || ""}
                    onChange={handleFormChange}
                    required
                  />
                </Form.Group>
              </>
            )}
            <Button
              type="button"
              onClick={() => handleAction(modalType)}
              disabled={processing}
            >
              {processing ? "Processing..." : "Update Details"}
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default ShippingPage;
